@use 'base/variables' as v;
@use 'base/mixins' as m;
.card-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "daniel daniel jonathan kira"
                         "jeanette patrick patrick kira";
    gap: 2rem;
    padding: 6rem;
}
.card {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    border-radius: 1rem;
    &__bio {
        display: flex;
        align-items: center;
        gap: 1rem;
        .imagen img{
            border-radius: 50%;
        }
    }
}
.daniel {
    position: relative;
    grid-area: daniel;
    background-color: hsla(263, 55%, 52%, .9);
    .quotes {
        position: absolute;
        top: 0;
        right: 2rem;
        width: 20rem;
        height: 15rem;
        background-image: url(../img/bg-pattern-quotation.svg);
        background-size: contain;
        background-repeat: no-repeat;
        z-index: 1;
    }
    p {
        color: $lightBlue;
        margin: 0;
    }
    .card__bio {
        padding: 1rem;
        .imagen {
            border-radius: 50%;
            border: .5rem solid hsla(263, 55%, 52%, 1.5);
        }
        .name {
            font-weight: 600;
        }
        .grad {
            font-weight: 500;
            opacity: .5;
        }
    }
    .card__info {
        position: relative;
        z-index: 2;
        .intro {
            font-size: 1.8rem;
            font-weight: 600;
            margin-block-end: 2rem;
        }
        .comment {
            opacity: .7;
        }
    }
}
