html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-size: 1.3rem;
  background-color: hsl(210, 46%, 95%);
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 1.3rem;
  color: #000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  display: block;
  max-width: 100%;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "daniel daniel jonathan kira" "jeanette patrick patrick kira";
  gap: 2rem;
  padding: 6rem;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border-radius: 1rem;
}
.card__bio {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.card__bio .imagen img {
  border-radius: 50%;
}

.daniel {
  position: relative;
  grid-area: daniel;
  background-color: hsla(263, 55%, 52%, 0.9);
}
.daniel .quotes {
  position: absolute;
  top: 0;
  right: 2rem;
  width: 20rem;
  height: 15rem;
  background-image: url(../img/bg-pattern-quotation.svg);
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 1;
}
.daniel p {
  color: hsl(210, 46%, 95%);
  margin: 0;
}
.daniel .card__bio {
  padding: 1rem;
}
.daniel .card__bio .imagen {
  border-radius: 50%;
  border: 0.5rem solid hsl(263, 55%, 52%);
}
.daniel .card__bio .name {
  font-weight: 600;
}
.daniel .card__bio .grad {
  font-weight: 500;
  opacity: 0.5;
}
.daniel .card__info {
  position: relative;
  z-index: 2;
}
.daniel .card__info .intro {
  font-size: 1.8rem;
  font-weight: 600;
  margin-block-end: 2rem;
}
.daniel .card__info .comment {
  opacity: 0.7;
}

