@use 'variables' as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuentePrincipal;
    font-size: 1.3rem;
    background-color: v.$lightBlue;
    &.fijar-body {
        overflow: hidden;
    }
}
p {
    font-size: 1.3rem;
    color: v.$negro;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
h1, h2, h3 {
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
img {
    display: block;
    max-width: 100%;
}